.App {
  display: flex;
  padding: 7.4rem 0;
  padding-bottom: 0;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 5.9rem;
  min-height: 100vh;
  padding-top: 0;
}
.App .DataWrapper {
  width: 100%;
  min-height: 100vh;
  padding-top: 7.4rem;
  display: flex;
  gap: 5.9rem;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.goUp {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 30;
}
.goUp .graphClose {
  font-size: 6rem;
  color: var(--primary-white);
  padding: 1rem;
  background-color: var(--black-2);
  border-radius: 50%;
  cursor: pointer;
}
