.Graph {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
}
.GraphContainer {
  padding: 4rem 6rem;
  border-radius: 2rem;
  z-index: 100;
  background-color: var(--black-1);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 5rem;
  overflow: hidden;
}
.GraphContainer .Graph-TitleCont {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.GraphContainer .Graph-TitleCont .Graph-Title {
  color: var(--secondary-white);
  font-family: "Poppins", sans-serif;
  font-size: 4rem;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  letter-spacing: 0.3rem;
}
.GraphContainer .Graph-TitleCont .daysCont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  position: relative;
  width: 20rem;
  height: 5rem;
}
.GraphContainer .Graph-TitleCont .daysCont span {
  font-family: "Poppins", sans-serif;
  font-size: 4rem;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.3rem;
  font-weight: 500;
  color: var(--green-3);
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  text-align: center;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease;
  cursor: pointer;
}
.GraphContainer .Graph-TitleCont .daysCont span:hover {
  scale: 1.03;
}
.GraphContainer .Graph-TitleCont .daysCont span:active {
  scale: 0.98;
}
.GraphContainer .Graph-TitleCont .daysCont .inActiveDay {
  font-size: 3rem;
  color: var(--black-2);
}
.GraphContainer .graphCloseCont {
  position: absolute;
  top: 2rem;
  right: 2rem;
}
.GraphContainer .graphCloseCont .graphClose {
  font-size: 5rem;
  color: var(--primary-white);
  padding: 1rem;
  background-color: var(--black-2);
  border-radius: 50%;
  cursor: pointer;
}
.GraphBackdrop {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 50;
}
